import { Component } from "react";


class Question extends Component {
    constructor(props){
        super(props)
        
        this.state = { 
            count : 0,
            end : false,
            score: 0
         }
// binding
         this.handleClick = this.handleClick.bind(this)
    }
    
//questions
    Questions = [
        {
            Quest : 'who was the father of Prophet Muhammad ?',
            answer : [
                {answerText : 'Abu talib', isCorrect : false,id:1} ,
               {answerText : 'Abdulmuttalib', isCorrect : false,id:2} ,
               {answerText : 'Hamzah', isCorrect : false,id:3} ,
               {answerText : 'Abdullah', isCorrect : true, id:4} ,
            ]
        },
        {
            Quest : 'who was the Mother of Prophet Muhammad ?',
            answer : [
                {answerText : "A'isha", isCorrect : false,id:1} ,
               {answerText : 'Amina', isCorrect : true,id:2} ,
               {answerText : 'Maryam', isCorrect : false, id:3} ,
               {answerText : 'Fatima', isCorrect : false, id:4 } ,
            ]
        },
        {
            Quest : "who was Prophet Muhammad's Best Friend ?",
            answer : [
                {answerText : 'Abu bakr', isCorrect : true,id:1} ,
               {answerText : 'Umar', isCorrect :  false,id:2},
               {answerText : 'Abu Huraira', isCorrect : false ,id:3} ,
               {answerText : 'Ali', isCorrect : false ,id:4} ,
            ]
        },
        {
            Quest : "At what Age did the Prophet die ?",
            answer : [
                {answerText : '40', isCorrect : false,id:1} ,
               {answerText : '70', isCorrect :  false,id:2},
               {answerText : '63', isCorrect : true ,id:3} ,
               {answerText : '58', isCorrect : false ,id:4} ,
            ]
        },{
            Quest : "Who among these was Prophet's Uncle?",
            answer : [
                {answerText : 'Abu bakr', isCorrect : false,id:1} ,
               {answerText : 'Umar', isCorrect :  false,id:2},
               {answerText : 'Abbas', isCorrect : true ,id:3} ,
               {answerText : 'Ali', isCorrect : false ,id:4} ,
            ]
        },
        {
            Quest : "which book is the most authentic book after AlQuran ?",
            answer : [
                {answerText : 'Sahih Muslim', isCorrect : false,id:1} ,
               {answerText : 'Riyaadussalihin', isCorrect :  false,id:2},
               {answerText : 'Sahih Bukari', isCorrect : true ,id:3} ,
               {answerText : 'Sahih ibn majah', isCorrect : false ,id:4} ,
            ]
        },
        {
            Quest : "who was the First Khalifah?",
            answer : [
                {answerText : 'Abu bakr', isCorrect : true,id:1} ,
               {answerText : 'Umar', isCorrect :  false,id:2},
               {answerText : 'Abu Huraira', isCorrect : false ,id:3} ,
               {answerText : 'Ali', isCorrect : false ,id:4} ,
            ]
        },
        {
            Quest : "who was the third Khalifah ?",
            answer : [
                {answerText : 'Abu bakr', isCorrect : false,id:1} ,
               {answerText : 'Umar', isCorrect :  false,id:2},
               {answerText : 'Uthman', isCorrect : true ,id:3} ,
               {answerText : 'Ali', isCorrect : false ,id:4} ,
            ]
        },
        {
            Quest : "who was the only man to have married two daughters of a prophet ?",
            answer : [
                {answerText : 'Abu bakr', isCorrect : false,id:1} ,
               {answerText : 'Umar', isCorrect :  false,id:2},
               {answerText : 'Uthman', isCorrect : true ,id:3} ,
               {answerText : 'Ali', isCorrect : false ,id:4} ,
            ]
        },
        {
            Quest : "who was the Cousin of the Prophet ?",
            answer : [
                {answerText : 'Abu bakr', isCorrect : false,id:1} ,
               {answerText : 'Umar', isCorrect :  false,id:2},
               {answerText : 'Abbas', isCorrect : false ,id:3} ,
               {answerText : 'Ali', isCorrect : true ,id:4} ,
            ]
        },

   ]
    // click handling
   handleClick(e){
        let correctIndex =  this.Questions[this.state.count].answer.findIndex(x => x.isCorrect === true)
        let correct = this.Questions[this.state.count].answer[correctIndex].id
        let choosen = e.target.id
        if(choosen == correct){
            this.setState({ score: this.state.score+1})
        }
     
        
    
       // if count plus one equals length of questions set end to true else increment count
        this.state.count+1 === this.Questions.length ? this.setState({ end : true }) :
        this.setState({count : this.state.count + 1 })

        
    }
       
   //findIndex(x => x.Country === 'Nigeria')

    render() { 
        
        const list =   
        <div className= 'quiz-container'>
            <div className='current-question'>
                Question <span id='current-question'>{this.state.count + 1}</span>/{this.Questions.length}
            </div>

            <div className='question'>
             {this.Questions[this.state.count].Quest }
            </div>

            <div className='ans'>
                {this.Questions[this.state.count].answer.map(answer => {
                     return <button id={answer.id} key= {answer.id} onClick= {this.handleClick}> {answer.answerText} </button>
                })}
            </div>
        
        </div>

        const scoreboard = 
        <div className = 'quiz-container score'>
            <div className ='score-text'>
                your Score is {this.state.score} out of {this.Questions.length}
            </div>
        </div>
       
       if(this.state.end){
        return (
            scoreboard
        )
    }
    else{ return(
        list
    )
    }
    }
}
 
export default Question;